@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap);
:root{--nav-width: 250px;--nav-bg: #222;--title-size: 1.1rem;--primary: #66e8ff}*{box-sizing:border-box;margin:0;padding:0}html{scroll-behavior:smooth}body{margin:0;padding:0;font-family:'Poppins', sans-serif;background:#ffffff;color:#000000}main{width:calc(100% - 250px - 2rem);width:calc(100% - var(--nav-width) - 2rem);margin-left:250px;margin-left:var(--nav-width);padding:1rem}h1,h2,h3,p,ul,li{margin:0;padding:0}p,ul{padding-bottom:1rem}ul{margin-left:1rem}a{text-decoration:none;color:#471caa}table{text-align:left;font-weight:normal;color:black;border-collapse:collapse;margin-bottom:1rem;width:100%}table,th,td{border:1px solid black}th,td{padding:0.5rem}tr:nth-child(even){background-color:#ffffff}tr:nth-child(odd){background-color:#dbdbdb}th{background-color:#a09fff;color:#000000}

:root{--nav-light: #292929}nav{position:fixed;top:0;left:0;height:100%;width:var(--nav-width);background:var(--nav-bg);color:white;box-shadow:0px 0 8px 2px rgba(0,0,0,0.8)}nav .title{text-align:center;font-weight:700;font-size:var(--title-size);padding:1rem;color:var(--primary);background:#292929;background:var(--nav-light);border-bottom:2px solid white}nav ul{height:100%;width:100%;list-style:none;margin:0}nav ul .barrier{height:10px;display:block;background:white}nav ul li{width:100%;line-height:50px;border-bottom:1px solid rgba(255,255,255,0.1);background:transparent;color:white;font-weight:400}nav ul li a{height:100%;padding-left:30px;position:relative;display:block;font-size:18px;font-weight:500;text-decoration:none;color:white;border-left:3px solid transparent}nav ul li a span{position:absolute;top:50%;right:20px;transform:translateY(-50%);font-size:24px;transition:transform 0.4s}nav ul li a span.rotated{transform:translateY(-50%) rotate(-180deg)}nav ul li a:hover{color:var(--primary);background:#292929;background:var(--nav-light);border-left:3px solid var(--primary)}nav ul li a:hover span{transform:translateY(-50%) rotate(-180deg)}nav ul li ul{position:static}nav ul li ul li{line-height:42px;border-bottom:none}nav ul li ul li a{font-size:16px;color:#e6e6e6;padding-left:60px}

h2{margin-top:1rem}p{padding-bottom:0;line-height:1.3rem}p,h3,h4{margin-top:0;margin-bottom:0}h4{font-size:1.2rem;margin-bottom:0.5rem}ul{padding-bottom:0;line-height:1.3rem}ul li a{font-weight:700}.heading{font-weight:700;margin-top:0.5rem}.sub-heading{font-weight:700}.topic{margin-bottom:1rem}

